"use client";

import { useEffect, useState, useCallback } from "react";
import api from "@/lib/axios";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Plus, Trash2, Edit, Signal, Wifi } from "lucide-react";

interface Product {
    id: number;
    network: string;
    name: string;
    price: number;
    data_amount?: number;
}

export default function AdminProductsPage() {
    const [products, setProducts] = useState<Product[]>([]);
    const [isOpen, setIsOpen] = useState(false);
    const [loading, setLoading] = useState(true);
    const [newProduct, setNewProduct] = useState({ network: '', name: '', price: '', data_amount: '' });

    const fetchProducts = useCallback(async () => {
        setLoading(true);
        try {
            const res = await api.get('/admin/products');
            setProducts(res.data);
        } catch (e) {
            console.error(e);
        } finally {
            setLoading(false);
        }
    }, []);

    useEffect(() => {
        fetchProducts();
    }, [fetchProducts]);

    const handleCreate = async () => {
        try {
            await api.post('/admin/products', newProduct);
            setIsOpen(false);
            setNewProduct({ network: '', name: '', price: '', data_amount: '' }); // Reset
            fetchProducts();
        } catch (e) {
            alert("Error creating product");
        }
    };

    const handleDelete = async (id: number) => {
        if (confirm("Are you sure you want to delete this bundle? This action cannot be undone.")) {
            try {
                await api.delete(`/admin/products/${id}`);
                setProducts(products.filter(p => p.id !== id));
            } catch (e) {
                alert("Failed to delete");
            }
        }
    };

    const groupedProducts = products.reduce((acc, product) => {
        const net = product.network.toUpperCase();
        if (!acc[net]) acc[net] = [];
        acc[net].push(product);
        return acc;
    }, {} as Record<string, Product[]>);

    const networks = Object.keys(groupedProducts);

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h2 className="text-2xl font-bold tracking-tight text-slate-800 dark:text-slate-100">Product Management</h2>
                    <p className="text-muted-foreground">Manage data bundles and pricing.</p>
                </div>

                <Dialog open={isOpen} onOpenChange={setIsOpen}>
                    <DialogTrigger asChild>
                        <Button className="gap-2 shadow-lg shadow-indigo-500/20">
                            <Plus className="w-4 h-4" /> Add Bundle
                        </Button>
                    </DialogTrigger>
                    <DialogContent className="sm:max-w-[425px]">
                        <DialogHeader>
                            <DialogTitle>Add New Bundle</DialogTitle>
                            <DialogDescription>
                                Create a new data package for your users.
                            </DialogDescription>
                        </DialogHeader>
                        <div className="grid gap-4 py-4">
                            <div className="grid gap-2">
                                <Label htmlFor="network">Network Provider</Label>
                                <Input
                                    id="network"
                                    placeholder="e.g. MTN, Telecel"
                                    value={newProduct.network}
                                    onChange={e => setNewProduct({ ...newProduct, network: e.target.value })}
                                />
                            </div>
                            <div className="grid gap-2">
                                <Label htmlFor="name">Bundle Name</Label>
                                <Input
                                    id="name"
                                    placeholder="e.g. 1GB Super Saver"
                                    value={newProduct.name}
                                    onChange={e => setNewProduct({ ...newProduct, name: e.target.value })}
                                />
                            </div>
                            <div className="grid grid-cols-2 gap-4">
                                <div className="grid gap-2">
                                    <Label htmlFor="price">Price (GHS)</Label>
                                    <Input
                                        id="price"
                                        type="number"
                                        placeholder="0.00"
                                        value={newProduct.price}
                                        onChange={e => setNewProduct({ ...newProduct, price: e.target.value })}
                                    />
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="data">Data (MB)</Label>
                                    <Input
                                        id="data"
                                        type="number"
                                        placeholder="1024"
                                        value={newProduct.data_amount}
                                        onChange={e => setNewProduct({ ...newProduct, data_amount: e.target.value })}
                                    />
                                </div>
                            </div>
                        </div>
                        <DialogFooter>
                            <Button onClick={handleCreate}>Create Bundle</Button>
                        </DialogFooter>
                    </DialogContent>
                </Dialog>
            </div>

            {loading ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 animate-pulse">
                    {[...Array(3)].map((_, i) => (
                        <div key={i} className="h-40 bg-slate-200 dark:bg-slate-800 rounded-lg"></div>
                    ))}
                </div>
            ) : networks.length === 0 ? (
                <div className="text-center py-12 text-muted-foreground border-2 border-dashed rounded-lg">
                    No products found. Create one to get started.
                </div>
            ) : (
                <Tabs defaultValue={networks[0]} className="w-full">
                    <TabsList className="mb-4">
                        {networks.map(net => (
                            <TabsTrigger key={net} value={net} className="capitalize">{net}</TabsTrigger>
                        ))}
                    </TabsList>

                    {networks.map(net => (
                        <TabsContent key={net} value={net} className="outline-none">
                            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
                                {groupedProducts[net].map(product => (
                                    <Card key={product.id} className="border-none shadow-[0_2px_10px_0_rgba(67,89,113,0.12)] hover:-translate-y-1 transition-transform duration-200">
                                        <CardHeader className="flex flex-row items-start justify-between pb-2">
                                            <div className="p-2 bg-slate-100 dark:bg-slate-800 rounded-lg">
                                                <Signal className="h-5 w-5 text-slate-600 dark:text-slate-400" />
                                            </div>
                                            <Badge variant="secondary" className="font-mono">
                                                GHS {product.price}
                                            </Badge>
                                        </CardHeader>
                                        <CardContent>
                                            <CardTitle className="text-lg mb-1">{product.name}</CardTitle>
                                            <p className="text-sm text-muted-foreground capitalize">
                                                {product.network} Bundle
                                            </p>
                                        </CardContent>
                                        <CardFooter className="border-t pt-4 flex gap-2 justify-end">
                                            <Button variant="ghost" size="icon" className="h-8 w-8 text-slate-400 hover:text-blue-600">
                                                <Edit className="h-4 w-4" />
                                            </Button>
                                            <Button
                                                variant="ghost"
                                                size="icon"
                                                className="h-8 w-8 text-slate-400 hover:text-red-600 hover:bg-red-50"
                                                onClick={() => handleDelete(product.id)}
                                            >
                                                <Trash2 className="h-4 w-4" />
                                            </Button>
                                        </CardFooter>
                                    </Card>
                                ))}
                            </div>
                        </TabsContent>
                    ))}
                </Tabs>
            )}
        </div>
    );
}
